import React, { Component } from 'react';
import icon_back from "../../../statics/back.png";
import { actionCreators } from '../store';
import { connect } from 'react-redux';
import {
    ChatWrapper,
    ChatTop, 
    Input, 
    ChatRoom,
    SendMsg
  } from '../style';

class Chat extends Component {
  render() {
    const {quitChat} = this.props;
    return (
      <ChatWrapper>
        <ChatTop onClick={(e) => quitChat(e)}>
        <img class="back" src={icon_back} alt=''/>
        </ChatTop>
        <ChatRoom>
      
        </ChatRoom>
        <div class="chat-bottom">
          <Input type="text" ref={(input) => {this.msg = input}} />
          <SendMsg LoginButton onClick={() => this.props.SendMsg()}
                >发送</SendMsg>
        </div>
      </ChatWrapper>
      )
    }
  }


  const mapState = (state) => ({
    comeChat: state.getIn(['home','comeChat'])
  });
  
  const mapDispatch = (dispatch) => ({
    quitChat(){
      dispatch(actionCreators.quitChat())
    },
  }); 
  
  
  export default connect(mapState, mapDispatch)(Chat);